@font-face {
  font-family: "Arial";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Arial"),
    url("../fonts/Arial.woff2") format("woff2"),
    url("../fonts/Arial.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Arial"),
    url("../fonts/Open-Sans.woff2") format("woff2"),
    url("../fonts/Open-Sans.woff") format("woff");
}



